.container {
  width: 100%;
  height: 100vh;
  background-color: #282c34;
  display: flex;
  align-items: center;
  justify-content: center;
}

.small-container {
  width: 500px;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}
.person {
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 10px;
  margin-bottom: 20px;
  align-items: center;
}
.person img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 10%;
}

.button {
  color: white;
  display: block;
  width: 50%;
  border-color: transparent;
  background: #282c34;
  margin-bottom: 50px;
  margin-top: 50px;
  text-transform: capitalize;
  font-size: 1.2rem;
  padding: 0.5rem 0;
  border-radius: 10px;
  cursor: pointer;
}
